import React, { Component } from 'react';
// import {BrowserRouter as Router,Redirect,Route,Link,NavLink,Switch} from 'react-router-dom'
import {Redirect,Route ,HashRouter as Router,Switch} from 'react-router-dom';
import '../App.css'
import Header from './Header';
import routes from './router.config';


function PrivateRoute({ component: Com, ...rest }) {
	return (
		<Route
			{ ...rest }
			render={ ({ location }) =>
				localStorage.getItem('userinfo')
					? <Com {...rest}  />
					: (
						<Redirect
							to={ {
								pathname: "/login",
								state: location
							} }
						/>
					)
			}
		/>
	);
}

export const  RouteWithSubRoutes = (route) => {
	if(route.pri){
		console.log('1');
		console.log(route.routes)
		return(
			<PrivateRoute {...route} routes={route.routes}></PrivateRoute>
		)
	}
	else{		
		return (
			<Route
			  path={route.path}
			  render={props => (
				<route.component {...props} routes={route.routes} />
			  )}
			/>
		  );
	}	
  }

const App = () => {
	return <Router >
		<Header />
		<Switch >
			{
				routes.map(({...rest})=>{
					// if(pri){
					// 	return <PrivateRoute {...rest} />
					// }
					return <RouteWithSubRoutes {...rest} />
				})
			}
		</Switch>		
	</Router>
}

export default App;
