import React,{useEffect} from 'react'
import {NavLink,Route,Redirect,
    useHistory,
    useLocation,
    useParams,
    useRouteMatch,
    withRouter,
    Switch,
    Router
} from 'react-router-dom'
import "./doc.css"
import Api from './Api'
import Guide from './Guide'
import {NavBar,Icon} from 'antd-mobile'
import {RouteWithSubRoutes} from './App'

const Doc = ({routes}) => {

    return (
        <div className= 'doc'>
        <div className = 'content'>
            {
                routes.map((route) => (
                    <RouteWithSubRoutes  {...route} />
                ))
            }
            <Redirect from='/doc' to='/doc/core'/>
            <ol>            
                <li><NavLink to='/doc/core'>核心概念</NavLink></li>
                <li><NavLink to='/doc/guide'>高级指引</NavLink></li>
                <li><NavLink to='/doc/api'>API</NavLink></li>
                <li><NavLink to='/doc/hook'>Hooks</NavLink></li>
            </ol> 
        </div>
        
    </div>
    )
}

export default withRouter(Doc);//高阶组件 